import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { fromEvent, Observable, of, from } from 'rxjs';
import { switchMap, debounceTime, distinctUntilChanged } from 'rxjs/operators';


@Component({
  selector: 'app-search-series',
  templateUrl: './search-series.component.html',
  styleUrls: ['./search-series.component.scss']
})
export class SearchSeriesComponent implements OnInit {
  private URL = 'http://api.tvmaze.com/search/shows?q=';

  @ViewChild('searchInput') searchInput: ElementRef;
  public series: object[];
  public isLoading = false;
  public isSeries = false;

  constructor() { }

  ngOnInit() {
    this.onKeyUpSubscription();
  }

  onKeyUpSubscription() {
    const input$: Observable<{}> = fromEvent(this.searchInput.nativeElement, 'keyup')
      .pipe(
        debounceTime(300),
        distinctUntilChanged()
      );

    input$.subscribe(() => {
      this.isSeries = true;
      this.isLoading = true;
      this.sendRequest();
    });
  }

  async sendRequest(): Promise<void | object[]> {
    const searchValue: string = this.searchInput.nativeElement.value;
    const res = await fetch(this.URL + searchValue);
    const data = await res.json();
    this.series = data;
    this.isSeries = !!this.series.length;
    this.isLoading = false;
  }
}
